.products-section {
    padding-top: 120px;
    padding-bottom: 98px;
    margin: 0 auto;
}

.pre-title {
    margin: 0 auto 15px;
    text-align: center;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    color: #FFA5BA;
}

.section-title {
    margin: 0 auto 30px;
    text-align: center;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
    @media screen and (min-width: 768px) {
        margin: 0 auto 40px;
        font-style: 48px;
    }
    @media screen and (min-width: 1280px) {
        margin: 0 auto 50px;
        font-size: 58px;
    }
}

.products__list {
align-items: center;
@media screen and (min-width: 768px) {
    display: flex;
    }
}
.products__item {
    position: relative;
    align-items: center;
    justify-content: center;
    height: 600px;
    padding-top: 115px;

    @media screen and (min-width: 1280px) {
        height: 665px;
        padding-top: 140px;
    }
}

.products__item:not(:last-child) {
    margin-bottom: 15px;

    @media screen and (min-width: 768px) {
        margin-bottom: 0;
    }
}

.products__item-card-cream {
    position: relative;
    margin: 0 auto;
    align-items: center;
    width: 280px;
    height: 485px;
    border: none;
    border-radius: 24px;
    background-color: #FFB8CA;

    @media screen and (min-width: 768px) {
        margin-right: 20px;
        width: 220px;
    }
    @media screen and (min-width: 1280px) {
        margin-right: 30px;
        width: 335px;
        height: 525px;
    }
    &:before {
        content: "";
        top: -125px;
        left: 15px;
        width: 250px;
        height: 250px;
        display: block;
        align-items: center;
        background-position: 50%;
        background-size: cover;
        position: absolute;

        background-image: url(../images/products/products1-mob-x1.png);

        @media screen and (min-width: 768px) {
            width: 220px;
            height: 226px;
            left: 0;
        }
        @media screen and (min-width: 1280px) {
            width: 330px;
            height: 335px;
            top: -150px;
        }
    }
}

