.pre-title-assortment {
font-family: Titan One;
font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 1.15;

letter-spacing: 0.04em;
text-transform: uppercase;
color: #FFFFFF; 
text-align: center;
margin-bottom: 30px;
padding-top: 160px;

@media screen and (min-width: 1280px) {
    padding-top: 210px;
    font-size: 30px;
    line-height: 1.13;
    }
}

.dots__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.dots__item:not(:last-child) {
    margin-right: 4px;
}

.dots {
    display: block;
    margin: 0 auto 30px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #FFFFFF;
}

.products__item-text {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    padding: 0 45px;
    height: auto;
    margin: 0 auto 40px;

    @media screen and (min-width: 768px) {
        padding: 0 15px;
        line-height: 1.76;
    }
    @media screen and (min-width: 1280px) {
        padding: 0 40px;
        font-size: 16px;
        line-height: 1.75;
        margin: 0 auto 30px;
    }
}

.products__btn {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 auto;
    background-color: #fff;
    background-image: url(../images/arrow.png);
    background-size: cover;
    background-repeat: no-repeat;
}

